// components/InfoButton.js

import Image from 'next/image';
import Link from 'next/link';
import styles from './page.module.css'; // Hvis du bruker stilene fra page.js

export default function InfoButton() {
  return (
    <Link href="/info">
      <button
        className="btn btn-primary d-flex align-items-center justify-content-start mb-3"
        style={{ padding: '10px' }}
      >
        <div>
          <Image
            src="/img/info.jpg"
            alt="Info"
            width={200}
            height={100}
            className="rounded-circle"
          />
          <span className={`${styles.customText}`}>Info</span>
        </div>
      </button>
    </Link>
  );
}
